@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Crimson+Text:wght@600&family=Lobster&family=Odibee+Sans&family=Pacifico&family=Square+Peg&family=Updock&display=swap');
:root {
    --fontback: rgb(156, 163, 36);
    --name: rgb(17, 67, 106);
}

body {
    margin: 0;
}

.area {
    margin: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    /* justify-content: center; */
    align-items: center;
    background-color: rgb(2, 4, 24);
    border: 2px solid black;
}

.box {
    width: 100vw;
    height: 50vh;
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgb(224, 223, 154);
    /* background-color: rgb(178, 178, 183); */
    border: 2px solid black;
}

.box h1 {
    font-family: 'Lobster', cursive;
    font-size: 5vw;
    margin: 35px;
    /* text-align: center; */
    /* box-shadow: inset 10px 10px 100px 10px var(--fontback); */
    /* color: white; */
    text-shadow: 2px 4px 5px var(--fontback), -2px -2px 5px var(--fontback), 0px -6px 5px var(--fontback), -6px 0px 5px var(--fontback), 50px 50px 500px var(--fontback), -50px -50px 500px var(--fontback), -50px 50px 500px var(--fontback);
    /* border: 2px solid yellow; */
    animation: jump ease-in-out;
    animation-duration: 1s;
    animation-iteration-count: infinite;
}

#cake {
    width: 100px;
}

.box h2 {
    font-size: 10vw;
    font-family: 'Abril Fatface', cursive;
    margin: 0px;
    color: rgb(169, 237, 237);
    /* text-align: center; */
    /* box-shadow: inset 10px 10px 100px 10px var(--fontback); */
    /* color: white; */
    text-shadow: 2px 4px 5px var(--name), -2px -2px 5px var(--name), 0px -6px 5px var(--name), -6px 0px 5px var(--name), 50px 50px 120px var(--name), -50px -50px 120px var(--name), -50px 50px 120px var(--name);
    /* border: 2px solid yellow; */
    /* animation: rot ease-in-out;
    animation-duration: 1s;
    animation-iteration-count: infinite; */
}

.boxcontainer {
    padding: 3vh;
    border: 10px dotted greenyellow;
    animation: rot 2s ease infinite;
    /* background-color: lightgreen; */
}

.happybox {
    display: flex;
    justify-content: center;
    /* background-color: lightcoral; */
}

.box h1:nth-child(1) {
    animation-delay: 0.5s;
}

.box h1:nth-child(2) {
    animation-delay: 1s;
}

@keyframes jump {
    50% {
        transform: translate(0px, -20px) scale(2);
        /* color: green; */
    }
    100% {
        transform: translateY(0px);
    }
}

@keyframes rot {
    50% {
        border: 10px dotted rgb(227, 98, 98);
    }
    100% {
        border: 10px dotted rgb(73, 202, 92);
    }
}